.timerMenuContainer {
    display: -ms-flexbox;
    display: flex;
    /* min-width: 150px; */
    background-color: #2f3542;
    min-height: 100vh;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; 
}

/* .drawerContainer {
    min-height:100%;
} */

.timerMenuContainer .drawerContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex:1 1;
        flex:1 1;
}

.open .timerDrawerContainer p, input {
    margin:0;
    margin-bottom: 10px;
}

.closed{
    max-width:25px;
}

.closed .timerDrawerContainer{
    display: none;
}

.open .timerDrawerContainer {
    /* margin: 10px 1px; */
    border-radius: 5px;
    background-color:#57606f;
    -ms-flex: 1 1;
        flex: 1 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.open .timerDrawerContainer .drawerTitle {
    color:white;
}

.open .timerDrawerContainer .timerType {
    color:#a4b0be;
}

.open .divider {
    min-width: 100%;
    min-height: 2px;
    background-color: #2f3542;
    border-radius: 5px;
    margin: 10px 0;
}

.timerMenuContainer .arrowContainer:hover{
    background-color: #3498db;
}

.open .timerContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align:center;
        align-items:center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.timerMenuContainer .time {
    text-align: center;
    border:none;
    padding:3px;
    width: 50px;
    background-color: #2f3542;
    border-radius: 2px;
    color:white;
}

.timerMenuContainer .arrow{
    padding: 0 5px;
}
.open .commentIcon {
    color: #2f3542;
}

.timerMenuContainer .commentIcon:hover{
    color:white;
}

.arrowContainer{
    border-radius: 3px;
    background-color: #57606f;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color:white;
    font-size: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
    margin-left: 2px;
}

.messageTextDialog {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position:fixed;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-item-align: center;
        align-self: center;
    -ms-flex: 1 1 80vh;
        flex: 1 1 80vh;
    height: 80vh; 
    width: 80vw;
    right: 10vw;
    -ms-flex-align: center;
        align-items: center;
    background-color: white;
    z-index: 1;
}

.messageTextDialog .closeDialogIcon {
    -ms-flex-item-align: end;
        align-self: flex-end;
    margin-right: 10px;
    color: #a4b0be;
}

.messageTextDialog .closeDialogIcon:hover {
    color:#2f3542;
}

.messageTextDialog textarea {
    padding: 5px;
    background-color: #2f3542;
    width: 100%;
    max-width: 97%;
    min-height: 70%;
    resize: none;
    color:white;
}

.messageTextDialog .saveBtn:hover {
    background-color: #4a69bd;
}

.messageTextDialog .saveBtn {
    background-color: #2f3542;
    border: none;
    border-radius: 5%;
    padding: 10px;
    color:white;
    cursor: pointer;
}

.hidden {
    display: none; 
    visibility: hidden;
}

.messageOverlay {
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.6);
    width: 100%;
    height: 100%;
    z-index: 1;
}

.visible {
    display: -ms-flexbox;
    display: flex;
    visibility: visible;
}

.drawerContainer .errorInSession {
    border: 2px solid red;
}

@media all and (max-height: 500px) {  
    .messageTextDialog textarea{
        min-height: 60%;
    }
}
