@media all and (max-width: 900px) and (max-height: 450px) {
    .open .timerDrawerContainer .drawerTitle {
        padding: 0;
        margin-bottom: 0;
    } 
    .timerDrawerContainer .timerContainer .commentIcon, .timerDrawerContainer .timerContainer p {
        padding:0;
        margin:0;
    }
    .timerDrawerContainer .divider {
        margin: 0;
    }
}


@media all and (max-width: 575px) and (max-height: 900px) {  
    .timerMenuContainer {
        width: 100%;
        min-height: 100%;
    }
    .timerMenuContainer .drawerContainer {
        -ms-flex-direction: column;
            flex-direction: column;        
    }
    .timerMenuContainer .timerDrawerContainer {
        margin-top: 0;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        margin-bottom: 0px;
        -ms-flex: 1 1 auto;
            flex: 1 1 auto;
        -ms-flex-align: baseline;
            align-items: baseline;
        -ms-flex-pack: distribute;
            justify-content: space-around;
    }
    .closed .timerDrawerContainer{
        display: none;
        /* min-height: 0; */
    }
    
    .timerMenuContainer.closed {
        min-width: 100%;
        -ms-flex: 1 1 50px;
            flex: 1 1 50px;
        height: 50px;
    }

    .closed .drawerContainer {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        height: 50px;
    }

    .arrowContainer{
        margin-top:1px;
        -ms-flex: 0 1 50px;
            flex: 0 1 50px;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: center;
            justify-content: center;
        min-width: 100%;
        height: 50px;
        margin: 0;
    }

    .timerMenuContainer .timerDrawerContainer>div {
        -ms-flex: 0 1 30%;
            flex: 0 1 30%;
    }

    .open .timerDrawerContainer .drawerTitle {
        -ms-flex: 1 0 100%;
            flex: 1 0 100%;
        display: -ms-flexbox;
        display: flex; 
        -ms-flex-pack: center; 
            justify-content: center;
        padding: 0;
        margin-bottom: 0;
    }
    .open .divider {
        display: none;
    }
}


